.card-front__name-title {\n        font-size: .7rem;\n    }\n\n    .card-front__name-suptitle {\n        font-size: .9rem;\n    }\n\n    // Card Date \\\\\n\n    .card-front__date {\n        @include positionAbs($top: 165px, $left: 240px);\n        width: 60px;\n        padding: 3px 8px;\n    }\n\n    \n    .card-front__date-title {\n        font-size: .7rem;\n    }\n\n    .card-front__date-info-month, .card-front__date-info-slash, .card-front__date-info-year {\n        font-size: .9rem;\n    }\n\n    .card-front__date-info-slash {\n        margin: 0 1px;\n    }\n\n\n}\n\n@media (max-width: 370px) {\n\n    // Card Number \\\\\n\n    .card-front__number {\n        @include positionAbs($top: 80px, $left: 5px);\n        width: 260px;\n        font-size: 1.2rem;\n\n    }\n\n    .card-front__number-item {\n        margin-right: 0;\n\n    }\n\n    // Card Name \\\\\n\n    .card-front__name {\n        @include positionAbs($top: 165px, $left: 5px);\n        width: 180px;\n    }\n\n    \n    .card-front__name-title {\n        font-size: .7rem;\n    }\n\n    .card-front__name-suptitle {\n        font-size: .9rem;\n    }\n\n    // Card Date \\\\\n\n    .card-front__date {\n        @include positionAbs($top: 165px, $left: 200px);\n        width: 60px;\n        padding: 3px 8px;\n    }\n\n    \n    .card-front__date-title {\n        font-size: .7rem;\n    }\n\n    .card-front__date-info-month, .card-front__date-info-slash, .card-front__date-info-year {\n        font-size: .9rem;\n    }\n\n    .card-front__date-info-slash {\n        margin: 0 1px;\n    }\n\n\n}\n\n","@mixin background($url) {\r\n    background-image: url($url);\r\n    background-repeat: no-repeat;\r\n    background-position: center center;\r\n    background-size: cover;\r\n}\r\n\r\n@mixin positionAbs($top, $left) {\r\n    position: absolute;\r\n    top: $top;\r\n    left: $left;\r\n}\r\n\r\n@mixin unselectable {\r\n    -moz-user-select: none;\r\n    -khtml-user-select: none;\r\n    -webkit-user-select: none;\r\n 